import React, { useState, useEffect } from "react";

import queryString from 'query-string';

import io from "socket.io-client";

import TextContainer from '../TextContainer/TextContainer';

import Messages from '../Messages/Messages';

import InfoBar from '../InfoBar/InfoBar';

import Input from '../Input/Input';

import './Chat.css';

let socket;

const Chat = ({ location }) => {
  const [name, setName] = useState('');
  const [room, setRoom] = useState('');
  const [users, setUsers] = useState('');
  const [message, setMessage] = useState('');
  const [messages, setMessages] = useState([]);
  const ENDPOINT = 'https://react-chat-4clef.herokuapp.com/';

  useEffect(() => {
    const { name, room } = queryString.parse(location.search);

    socket = io(ENDPOINT);

    setRoom(room);
    setName(name)

    socket.emit('join', { name, room }, (error) => {
      if(error) {
        alert(error);
      }
    });
  }, [ENDPOINT, location.search]);

  useEffect(() => {
    socket.on('message', (message) => {
      setMessages([...messages, message ]);
    });

    socket.on('roomData', ({ users }) => {
      setUsers(users);
    })


//on unmount of the component close the hook

    return () => {
      socket.emit('disconnect');

//close the connection so no dealing with it afterward
      socket.off();
    }
  }, [messages])


//function for sending messages

  const sendMessage = (event) => {
//stopping the event of pressing any key and refreshing the page
    event.preventDefault();

    if(message) {
      socket.emit('sendMessage', message, () => setMessage(''));
    }
  }
// console.log(message, messages);
  return (
    <div className="outerContainer"> 
      <div className="container">
          <InfoBar room={room} />
          <Messages messages={messages} name={name} />
          <Input message={message} setMessage={setMessage} sendMessage={sendMessage} />
      </div>
      <TextContainer users={users}/>
    </div>
  );
}

export default Chat;
